body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  /* display: flex; */
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.wrapper{
  width: 400px;
  height: 90%;
  background-color: black;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  /* padding: 10px; */
  justify-content: space-evenly;
}

.input-field{
  
  display: flex;
  flex-direction: column;
  color: white;
  padding: 15px;
}

#expression{
  height: 40px;
  font-size: 25px;
  text-align: right;
}

#result{
  height: 50px;
  font-size: 50px;
  text-align: right;
}
.button{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: repeat(5,1fr);
  justify-items: center;
  grid-row-gap: 10px;
  font-size: 30px;
  padding: 15px;
}


.btn{
  background-color: #ffb1b1;
  color: white;
  height: 80px;
  width: 80px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.btn:hover{
  filter: brightness(120%);
  cursor: pointer;
} 

.btn-zero{
  grid-column: 1/ 3;
  width: 160px;
  border-radius: 40px;
}

.btn-light-gray{
  background-color: #a5a5a5;
}

.btn-dark-gray{
  background-color: #333333;
}

.btn-yellow{
  background-color: #ff9f0a;
}